.btn {
	display: inline-block;
	vertical-align: top;

	padding: 0 13px;
	margin: 0;
	border: none;
	border-radius: 3px;
	min-width: 40px;
	height: 38px;

	color: #2b2b2b;
	background: #d5d5d5;

	font-family: $boost;
	font-size: 1rem;
	font-weight: 700;
	line-height: 38px;
	letter-spacing: .001em;

	text-align: center;
	text-decoration: none;

	white-space: nowrap;

	transition: background 0.175s ease, box-shadow 0.175s ease, opacity 0.175s ease;

	cursor: pointer;
	user-select: none;
	&:hover {
		background: darken(#d5d5d5, 5%); }
	&:active {
		background: darken(#d5d5d5, 10%); }
	&_secondary {
		color: $white;
		background: $secondary;
		&:hover {
			background: darken($secondary, 5%); }
		&:active {
			background: darken($secondary, 10%); } }
	&_wide {
		width: 100%; } }
