//FONTS
@mixin fontpath($name, $name1, $file) {
	src: local($name), local($name1), url("../fonts/" + $file + ".woff2") format("woff2"), url("../fonts/" + $file + ".woff") format("woff"); }
@mixin fontdefault($style, $weight, $name) {
	font-family: $name;
	font-style: $style;
	font-weight: $weight; }

@font-face {
	@include fontdefault(normal, 400, $chalet);
	@include fontpath('Chalet LondonNineteenSixty', 'Chalet-LondonNineteenSixty', chalet_london_nineteen_sixty); }

@font-face {
	@include fontdefault(normal, 100, $boost);
	@include fontpath('Boost Next Thin', 'BoostNext-Thin', BoostNext-Thin); }
@font-face {
	@include fontdefault(normal, 300, $boost);
	@include fontpath('Boost Next Light', 'BoostNext-Light', BoostNext-Light); }
@font-face {
	@include fontdefault(normal, 400, $boost);
	@include fontpath('Boost Next Regular', 'BoostNext-Regular', BoostNext-Regular); }
@font-face {
	@include fontdefault(normal, 700, $boost);
	@include fontpath('Boost Next Bold', 'BoostNext-Bold', BoostNext-Bold); }
@font-face {
	@include fontdefault(normal, 800, $boost);
	@include fontpath('Boost Next Heavy', 'BoostNext-Heavy', BoostNext-Heavy); }
