html,
body,
.main {
  position: relative;
}

html {
  font-size: 16px;
}

body {
  color: #2a2a2a;
  background: #eaeae9;
  font-family: "Chalet LondonNineteenSixty";
  font-size: 0.875rem;
  line-height: 1.4;
}

.label {
  font-size: 1rem;
}

strong {
  font-family: "Boost Next";
  font-weight: 800;
}

.btn {
  display: inline-block;
  vertical-align: top;
  padding: 0 13px;
  margin: 0;
  border: none;
  border-radius: 3px;
  min-width: 40px;
  height: 38px;
  color: #2b2b2b;
  background: #d5d5d5;
  font-family: "Boost Next";
  font-size: 1rem;
  font-weight: 700;
  line-height: 38px;
  letter-spacing: .001em;
  text-align: center;
  text-decoration: none;
  white-space: nowrap;
  transition: background 0.175s ease, box-shadow 0.175s ease, opacity 0.175s ease;
  cursor: pointer;
  user-select: none;
}

.btn:hover {
  background: #c8c8c8;
}

.btn:active {
  background: #bcbcbc;
}

.btn_secondary {
  color: #fff;
  background: #9784ff;
}

.btn_secondary:hover {
  background: #816bff;
}

.btn_secondary:active {
  background: #6c51ff;
}

.btn_wide {
  width: 100%;
}

[data-animation*="ripple"] {
  position: relative;
  overflow: hidden;
}

[data-animation*="ripple"] .ripple-circle {
  position: absolute;
  z-index: 20;
  top: 50%;
  left: 50%;
  border-radius: 50%;
  width: 112px;
  height: 112px;
  background: rgba(0, 0, 0, 0.1);
  animation: ripple 0.5s linear forwards;
  pointer-events: none;
}

@keyframes ripple {
  0% {
    opacity: 1;
    transform: scale(0);
  }
  100% {
    opacity: 0;
    transform: scale(2);
  }
}

[data-scrollbar="rounded"]::-webkit-scrollbar,
[data-scrollbar="default"]::-webkit-scrollbar {
  display: initial !important;
}

[data-scrollbar="rounded"]::-webkit-scrollbar-thumb,
[data-scrollbar="default"]::-webkit-scrollbar-thumb {
  background: #141547;
}

.no-touch [data-scrollbar="rounded"]::-webkit-scrollbar-thumb:hover, .no-touch
[data-scrollbar="default"]::-webkit-scrollbar-thumb:hover {
  background: #141547;
}

[data-scrollbar="rounded"]::-webkit-scrollbar:vertical {
  width: 12px;
  background: linear-gradient(to right, transparent 5px, #9784ff 5px, #9784ff 6px, transparent 6px);
}

[data-scrollbar="rounded"]::-webkit-scrollbar:horizontal {
  height: 12px;
  background: linear-gradient(to bottom, transparent 5px, #9784ff 5px, #9784ff 6px, transparent 6px);
}

[data-scrollbar="rounded"]::-webkit-scrollbar-thumb {
  border-radius: 6px;
}

[data-scrollbar="default"]::-webkit-scrollbar:vertical {
  width: 7px;
  background: linear-gradient(to right, transparent 3px, #9784ff 3px, #9784ff 4px, transparent 4px);
}

[data-scrollbar="default"]::-webkit-scrollbar:horizontal {
  height: 7px;
  background: linear-gradient(to bottom, transparent 3px, #9784ff 3px, #9784ff 4px, transparent 4px);
}

.box {
  padding: 15px 10px;
  background: #f2f3f4;
  font-size: 0;
}

.box__item {
  display: inline-block;
  vertical-align: top;
  padding: 5px 10px;
  width: 50%;
  font-size: 1rem;
}

.box__item:after {
  content: " ";
  display: table;
  clear: both;
}

.box__label, .box__value {
  float: left;
}

.box__label {
  width: 60%;
}

.box__value {
  width: 40%;
}

.content {
  padding: 15px 0;
}

.content__row {
  margin: 0 -10px;
  font-size: 0;
}

.content__col {
  display: inline-block;
  vertical-align: top;
  padding: 10px;
  width: 50%;
  font-size: 0.9375rem;
}

.content__label {
  margin-bottom: 5px;
}

.content a {
  color: inherit;
  text-decoration: underline;
}

.content a:hover {
  text-decoration: none;
}

.footer {
  padding-top: 10px;
  border-top: 1px solid #d5d5d5;
}

.footer:after {
  content: " ";
  display: table;
  clear: both;
}

.footer__content {
  float: left;
  padding-right: 120px;
  margin-right: -100px;
  width: 100%;
  color: #141547;
}

.footer__btn {
  float: left;
  padding-top: 8px;
  width: 100px;
}

.footer__title {
  float: left;
  padding-right: 20px;
  font-size: 1.25rem;
}

.footer__text {
  overflow: hidden;
  padding-top: 8px;
}

.about {
  padding: 20px 20px 0;
